import React from 'react';
import css from './itinerary-styles.css';
import backgroundHeight from '../common/backgroundHeight.js'

class Itinerary extends React.Component {

    constructor(props){
        super(props);
        
    }    
    componentDidMount(){
        window.addEventListener('load', this.onLoad);
    }

    onLoad = () => {
        backgroundHeight(this.backgroundHeight);
        console.log('here');
        // var height = window.innerHeight;   
        // this.refs.height.setAttribute("style","height:" + height + "px");
    }


    render() {
    return (
        <div ref={(input) => {this.backgroundHeight = input}} className={css.setArea}>
            <div className={css.inner}>
                <div className={css.innerinner}>
                Itinerary
                </div>
            </div>
        </div>
    )
}
}

export default Itinerary;


